.navigation {
  height: 130px;
  width: 100%;
  display: flex;
  align-items: center;
  position: relative;
  padding: 0.5rem 0rem;

  color: black;
}
.brand-name {
  text-decoration: none;
  text-decoration-color: wheat;
  background: -webkit-linear-gradient(#eee, #1c1b1b);
  font-size: 190%;
  margin-left: 5rem;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.navigation-menu {
  margin-left: auto;
}
.navigation-menu ul {
  display: flex;
  padding: 50px;
}
.navigation-menu li {
  list-style-type: none;
  text-decoration-color: wheat;
  margin: 0 1rem;
}
.navigation-menu li a {
  text-decoration: none;
  display: block;
  width: 100%;
  color: #1c1b1b;
}
.navigation-menu li a:hover {
  background-color: lightblue;
}
.hamburger {
  border: 0;
  height: 40px;
  width: 40px;
  padding: 0.5rem;
  border-radius: 50%;
  background: transparent;

  cursor: pointer;
  transition: background-color 0.2s ease-in-out;
  position: absolute;
  top: 50%;
  right: 25px;
  transform: translateY(-50%);
  display: none;
}
.hamburger:hover {
  background-color: blue;
}
